import { AxisLeft, AxisBottom } from "@visx/axis";
import { scaleTime, scaleLinear, scaleOrdinal } from "@visx/scale";
import { LinePath } from "@visx/shape";
import { min, max } from "d3-array";
import * as _ from "lodash";
import Tippy from "@tippyjs/react";
import "tippy.js/dist/tippy.css"; // optional

export default function Chart(props) {
  const filteredData = _.filter(props.data, function (o) {
    return (
      o.nome_infraestrutura === props.selectedInfra &&
      o.medida === "percentagem"
    );
  });

  const width = window.screen.width;
  var margin = { top: 50, right: 60, bottom: 40, left: 70 },
    width_ = width - margin.left - margin.right,
    height = 400 - margin.top - margin.bottom;

  let time = [];
  filteredData.map((el) => time.push((el["date_object"] = new Date(el.data))));

  const timeScale = scaleTime({
    domain: [min(time), max(time)],
    range: [0, width_],
  });
  const linearScale = scaleLinear({
    domain: [0, 100],
    range: [height, 0],
  });

  return (
    <div height={400} width={width} style={{ margin: "10px" }}>
      <svg width={width} height={400}>
        {filteredData.map((el) => (
          <>
            <Tippy content={el.resumo_infraestrutura}>
              <circle
                cx={timeScale(el.date_object) + 30}
                cy={linearScale(el.resumo_infraestrutura)}
                r={1}
                stroke="black"
              ></circle>
              {/* <LinePath
            //   strokeWidth={3}
            data={filteredData}
            x={(el) => timeScale(el.date_object) + 30}
            y={(el) => linearScale(el.resumo_infraestrutura)}
            stroke={"black"}
          /> */}
            </Tippy>
          </>
        ))}

        <AxisLeft scale={linearScale} left={30}></AxisLeft>
        <AxisBottom
          scale={timeScale}
          top={height}
          bottom={height}
          numTicks={10}
          left={30}
          height={height}
        ></AxisBottom>
      </svg>
    </div>
  );
}
